@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    color: black;
  }
}
@keyframes fadein1 {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
@keyframes fadeout {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
html {
  background-color: black;
}
.background {
  display: flex;
  flex-wrap: wrap;
}

.button{
    background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
    color:white;
    display:block;
    margin-left: auto;
    margin-right:auto;
    animation-name: fadein1;
    animation-duration: 3s;
    animation-iteration-count: 1;
}

.main-text1 {
  color: rgb(215, 247, 248);
  font-size: 20px;
  font-family: "poppins", sans-serif;
}
.main-text2 {
  color: white;
  font-size: 30px;
  font-family: "poppins", sans-serif;
}

.text-holder {
  margin: 1rem;
  width: 100%;
  height: 20vh;
}
.image-holder {
  width: full;
  margin-top: 4rem;
  margin-left: auto;
  margin-right: auto;
}
.main-text-underline {
  color: white;
  border: white solid 2px;
  border-top: 0px;
  border-left: 0px;
  border-right: 0px;
  width: 10rem;
}

.image {
    height: 20rem;
    width: 20rem;
    border-radius: 100%;
    animation-name: fadein;
    animation-duration: 5s;
    animation-iteration-count: 1;
    object-fit: contain;
  }

.image1 {
  height: 4rem;
  width: 4rem;
  border-radius: 100%;
  animation-name: fadein;
  animation-duration: 5s;
  animation-iteration-count: 1;
}
.heart {
  width: max-content;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 480px) {
  .background {
    width: max-content;
    display: flex;
    flex-wrap: wrap;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-left: auto;
    margin-right: auto;
  }
  .main-text1 {
    color: rgb(215, 247, 248);
    font-size: 20px;
    font-family: "poppins", sans-serif;
  }
  .main-text2 {
    color: white;
    font-size: 30px;
    font-family: "poppins", sans-serif;
  }
  .text-holder {
    margin: 1rem;
    width: 20rem;
    padding-top: 20vh;
  }
  .image-holder {
    width: full;
    margin-top: 4rem;
    margin-left: auto;
    margin-right: auto;
  }
  .image {
    height: 20rem;
    width: 20rem;
    border-radius: 100%;
    animation-name: fadein;
    animation-duration: 5s;
    animation-iteration-count: 1;
    object-fit: contain;
  }
}
